.about {
    width: 95%;
    margin: auto;

    .containerAbout {
        display: flex;
        gap: 5rem;
        color: $color1;
        padding-left: 5rem;
        padding-right: 5rem;
        padding-top: 8rem;
        padding-bottom: 8rem;
        backdrop-filter: blur(10px);
        border-radius: 15px;

        .imgMe {
            width: 30rem;
            height: 35rem;
            margin-top: 1.5rem;
            border-radius: 3.5rem;
            // -webkit-box-shadow: 10px 10px 15px -10px $color4Op;
            // -moz-box-shadow: 10px 10px 15px -10px $color4Op;
            // box-shadow: 10px 10px 15px -10px $color4Op;

            @include lg-telefono {
                display: none;
            }

            @include tablet {
                width: 25rem;
                height: 29rem;
            }
        }

        p {
            text-align: justify;
            font-size: 2rem;
            font-family: $font1;

            @include tablet {
                font-size: 1.8rem;
            }

            @include lg-telefono {
                font-size: 1.4rem;
            }

            &:last-of-type {
                margin-bottom: 2rem;
            }
        }

        @include lg-tablet {
            flex-direction: column;
            align-items: center;
        }

        @include tablet {
            padding-left: 2rem;
            padding-right: 2rem;
            padding-top: 4rem;
            padding-bottom: 4rem;
        }

    }
}