*,
*:before,
*:after {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

html,
body {
    width: 100%;
    height: 100%;
}

body {
    margin: 0;
    text-align: center;
    font-family: $font2;
}

main {
    max-width: 140rem;
    margin: auto;
}

canvas {
    display: block;
    position: fixed;
    z-index: -999;
}

h3 {
    color: $color5;
    font-size: 3.5rem;
    padding-top: 10rem;
    padding-bottom: 10rem;

    @include telefono {
        font-size: 2rem;
        padding-top: 4rem;
        padding-bottom: 4rem;
    }

    @include lg-telefono {
        font-size: 2.2rem;
        padding-top: 4.2rem;
        padding-bottom: 4.2rem;
    }

    @include tablet{
        font-size: 2.8rem;
        padding-top: 5rem;
        padding-bottom: 5rem;
    }
}