/** Media Queries **/
@mixin sm-telefono {
    @media (max-width: #{$sm-telefono}){
        @content;
    }
}

@mixin telefono {
    @media (max-width: #{$telefono}) {
        @content;
    }
}

@mixin lg-telefono {
    @media (max-width: #{$lg-telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: #{$tablet}) {
        @content;
    }
}

@mixin lg-tablet {
    @media (max-width: #{$lg-tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (max-width: #{$desktop}) {
        @content;
    }
}