.tec {
    width: 95%;
    margin: auto;
    margin-top: 10rem;
    padding-bottom: 10rem;
    display: flex;

    .imgTec {
        flex: none;
        width: 15rem;
        height: 15rem;
        margin-top: 1.2rem;
        border-radius: 2.5rem;
        -webkit-box-shadow: 10px 10px 15px -10px $color4Op;
        -moz-box-shadow: 10px 10px 15px -10px $color4Op;
        box-shadow: 10px 10px 15px -10px $color4Op;

        &:hover {
            -webkit-box-shadow: 0px 0px 30px 8px $color4Op;
            -moz-box-shadow: 0px 0px 30px 8px $color4Op;
            box-shadow: 0px 0px 30px 8px $color4Op;
        }

        @include lg-tablet {
            width: 13rem;
            height: 13rem;
            margin-top: 0.8rem;
        }

        @include tablet {
            width: 12rem;
            height: 12rem;
            margin-top: 0.8rem;
        }

        @include lg-telefono {
            width: 10rem;
            height: 10rem;
            margin-top: 0.7rem;
        }

        @include telefono {
            width: 8rem;
            height: 8rem;
            margin-top: 0.6rem;
            border-radius: 1rem;
        }
    }

    @include desktop {
        flex-direction: column;
    }
}


.tecImgConteiner {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 3.5rem;
    margin: auto;

    @include tablet {
        gap: 2rem
    }
}

.divImgTecA, .divImgTecB  {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .polygon {
        clip-path: polygon(100% 0, 100% 80%, 60% 80%, 50% 100%, 40% 80%, 0 80%, 0 0);
        background-color: $color7;
        width: 15rem;
        height: 7.5rem;
        opacity: 0;
        transition: opacity, 1s;

        @include lg-tablet {
            width: 13rem;
            height: 6.5rem;
        }

        @include tablet {
            width: 12rem;
            height: 6rem;
        }

        @include lg-telefono {
            width: 10rem;
            height: 5rem;
        }

        @include telefono {
            width: 8rem;
            height: 4rem;
        }

        .levelHTML,
        .levelCSS,
        .levelBOOT,
        .levelJS,
        .levelSASS,
        .levelJquery,
        .levelPHP,
        .levelGIT,
        .levelSQL,
        .levelGULP,
        .levelTERM,
        .levelMySQL,
        .levelREACT {
            height: 25%;
            width: 90%;
            margin-top: 2.06rem;
            margin-left: 5%;
            border-radius: 10px;

            @include lg-tablet {
                margin-top: 2.06rem;
            }

            @include tablet {
                margin-top: 1.65em;
            }

            @include lg-telefono {
                margin-top: 1.4rem;
            }
        }

        .levelHTML,
        .levelCSS {
            background: rgb(0, 255, 0);
            background: linear-gradient(90deg, rgba(0, 255, 0, 1) 0%, rgba(0, 255, 0, 1) 33%, rgba(0, 255, 0, 1) 67%, rgba(16, 228, 0, 1) 100%);
        }

        .levelJS,
        .levelSASS,
        .levelTERM {
            background: rgb(0, 255, 0);
            background: linear-gradient(90deg, rgba(0, 255, 0, 1) 0%, rgba(0, 255, 0, 0.8491771708683473) 33%, rgba(0, 255, 0, 0.2497373949579832) 67%, rgba(0, 255, 0, 0) 100%);

        }

        .levelGIT,
        .levelPHP,
        .levelSQL,
        .levelMySQL {
            background: rgb(255, 255, 0);
            background: linear-gradient(90deg, rgba(255, 255, 0, 1) 0%, rgba(255, 255, 0, 0.8547794117647058) 33%, rgba(255, 255, 0, 0.09567577030812324) 67%, rgba(255, 255, 0, 0) 100%);
        }

        .levelBOOT,
        .levelGULP,
        .levelJquery {
            background: rgb(255, 255, 0);
            background: linear-gradient(90deg, rgba(255, 255, 0, 0.7987570028011204) 0%, rgba(255, 255, 0, 0.19931722689075626) 33%, rgba(255, 255, 0, 0) 67%, rgba(255, 255, 0, 0) 100%);
        }

        .levelREACT {
            background: rgb(255, 0, 0);
            background: linear-gradient(90deg, rgba(255, 0, 0, 0.7035189075630253) 0%, rgba(250, 0, 0, 0.0984768907563025) 33%, rgba(255, 0, 0, 0) 67%, rgba(255, 0, 0, 0) 100%);
        }
    }

    &:hover .polygon {
        opacity: 100;
    }
}

.tecA-Slide{
    display: flex;
    flex-wrap: nowrap;
}