@font-face {
  font-family: "foundationtitleshandmedium";
  src: url("font/foundationtitleshand-v0.85-webfont.woff2") format("woff2"), url("font/foundationtitleshand-v0.85-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "kerox-noncommercialregular";
  src: url("font/kerox-noncommercial-webfont.woff2") format("woff2"), url("font/kerox-noncommercial-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
/** Media Queries **/
*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  text-align: center;
  font-family: "foundationtitleshandmedium";
}

main {
  max-width: 140rem;
  margin: auto;
}

canvas {
  display: block;
  position: fixed;
  z-index: -999;
}

h3 {
  color: rgb(255, 211, 144);
  font-size: 3.5rem;
  padding-top: 10rem;
  padding-bottom: 10rem;
}
@media (max-width: 480px) {
  h3 {
    font-size: 2rem;
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}
@media (max-width: 620px) {
  h3 {
    font-size: 2.2rem;
    padding-top: 4.2rem;
    padding-bottom: 4.2rem;
  }
}
@media (max-width: 768px) {
  h3 {
    font-size: 2.8rem;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

::-webkit-scrollbar {
  width: 1.2rem;
}

::-webkit-scrollbar-corner {
  background-color: #151515;
  border-top-left-radius: -10px;
  border-top-right-radius: -10px;
}

::-webkit-scrollbar-track {
  background-color: #151515;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(255, 211, 144);
  border-radius: 10px;
  border: 2px solid #151515;
}

header {
  width: 100%;
  height: 5rem;
  position: fixed;
  background-color: #151515;
  z-index: 9999;
}

.containerHeader {
  max-width: 140rem;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}
.containerHeader .nav {
  max-width: 140rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 3.5rem;
  height: 100%;
}
.containerHeader .nav a {
  text-decoration: none;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 2px;
  transition: background-size 0.3s;
  font-size: 2rem;
  color: rgb(255, 211, 144);
}
.containerHeader .nav a:hover {
  background-size: 100% 2px;
  font-weight: bold;
  color: #ff7300;
}
@media (max-width: 620px) {
  .containerHeader .nav a {
    font-size: 1.5rem;
  }
}
.containerHeader .nav .focusing {
  background-position: 0% 100%;
  background-size: 100% 2px;
  font-weight: bold;
  color: #ff7300;
}
@media (max-width: 768px) {
  .containerHeader .nav {
    flex-direction: column;
    align-items: flex-end;
    background-color: #151515;
    position: fixed;
    overflow-y: auto;
    margin: 0;
    right: -80%;
    height: 27rem;
    top: 5rem;
    padding-bottom: 3.5rem;
    padding-right: 2rem;
    transition: right 0.3s;
  }
}
@media (max-width: 620px) {
  .containerHeader .nav {
    height: 16rem;
    gap: 2rem;
    padding-bottom: 2rem;
  }
}
.containerHeader .logo {
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  font-family: "kerox-noncommercialregular";
  font-size: 3rem;
  color: rgb(255, 211, 144);
  padding-bottom: 0.5rem;
  border: 0.1rem solid rgb(255, 211, 144);
  border-radius: 50%;
  width: 4.5rem;
}
.containerHeader .logo:hover {
  color: #ff7300;
  border: 0.1rem solid #ff7300;
}
@media (max-width: 620px) {
  .containerHeader .logo {
    font-size: 2rem;
    width: 2.97rem;
  }
}
.containerHeader .nav-toggle {
  color: rgb(255, 211, 144);
  background: none;
  border: none;
  font-size: 30px;
  cursor: pointer;
  display: none;
}
@media (max-width: 768px) {
  .containerHeader .nav-toggle {
    display: block;
  }
  .containerHeader .nav-toggle:focus:not(:focus-visible) {
    outline: none;
  }
}
@media (max-width: 768px) {
  .containerHeader .nav-menu_visible {
    padding-right: 2rem;
    right: 0;
    padding-left: 1rem;
  }
}

.start {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.start .containerTitle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.start .containerTitle .titleN, .start .containerTitle .titleL {
  color: rgb(255, 211, 144);
  font-family: "kerox-noncommercialregular";
  font-size: 10rem;
  margin-top: 0;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
@media (max-width: 1024px) {
  .start .containerTitle .titleN, .start .containerTitle .titleL {
    font-size: 8rem;
  }
}
@media (max-width: 896px) {
  .start .containerTitle .titleN, .start .containerTitle .titleL {
    font-size: 6rem;
  }
}
@media (max-width: 768px) {
  .start .containerTitle .titleN, .start .containerTitle .titleL {
    font-size: 5rem;
  }
}
.start .containerTitle .titleSep {
  margin: 1rem 2rem;
}
@media (max-width: 480px) {
  .start .containerTitle .titleSep {
    display: none;
  }
}
@media (max-width: 620px) {
  .start .containerTitle {
    flex-direction: column;
  }
}
.start .containerSubtitle {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}
.start .containerSubtitle .subtitle {
  color: rgb(255, 211, 144);
  font-family: "kerox-noncommercialregular";
  font-size: 4rem;
  margin: 0;
  text-transform: uppercase;
}
@media (max-width: 1024px) {
  .start .containerSubtitle .subtitle {
    font-size: 3rem;
  }
}
@media (max-width: 896px) {
  .start .containerSubtitle .subtitle {
    font-size: 2rem;
  }
}
@media (max-width: 768px) {
  .start .containerSubtitle .subtitle {
    font-size: 2rem;
  }
}
@media (max-width: 480px) {
  .start .containerSubtitle .subtitle {
    font-size: 2rem;
  }
}

.about {
  width: 95%;
  margin: auto;
}
.about .containerAbout {
  display: flex;
  gap: 5rem;
  color: #f8f0cd;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-top: 8rem;
  padding-bottom: 8rem;
  backdrop-filter: blur(10px);
  border-radius: 15px;
}
.about .containerAbout .imgMe {
  width: 30rem;
  height: 35rem;
  margin-top: 1.5rem;
  border-radius: 3.5rem;
}
@media (max-width: 620px) {
  .about .containerAbout .imgMe {
    display: none;
  }
}
@media (max-width: 768px) {
  .about .containerAbout .imgMe {
    width: 25rem;
    height: 29rem;
  }
}
.about .containerAbout p {
  text-align: justify;
  font-size: 2rem;
  font-family: "helvetica nuew", helvetica;
}
@media (max-width: 768px) {
  .about .containerAbout p {
    font-size: 1.8rem;
  }
}
@media (max-width: 620px) {
  .about .containerAbout p {
    font-size: 1.4rem;
  }
}
.about .containerAbout p:last-of-type {
  margin-bottom: 2rem;
}
@media (max-width: 896px) {
  .about .containerAbout {
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 768px) {
  .about .containerAbout {
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}

@media (max-width: 1024px) {
  .pro {
    margin-top: 2rem;
  }
}

.proImgConteiner {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  margin: 0 0 20rem 0;
}
@media (max-width: 1024px) {
  .proImgConteiner {
    margin: auto;
  }
}

.imgConteinerLink {
  height: 35rem;
  margin: 5rem 0 5rem 0;
  position: relative;
  border-radius: 2rem;
}
.imgConteinerLink .imgProject {
  height: 100%;
  border-radius: 2rem;
  opacity: 100%;
  transition: opacity, 1s;
}
.imgConteinerLink:hover .imgProject {
  opacity: 40%;
}
.imgConteinerLink a {
  text-decoration: none;
  font-size: 2rem;
  color: #f5f5f5;
}
@media (max-width: 620px) {
  .imgConteinerLink a {
    font-size: 1rem;
  }
}
.imgConteinerLink i {
  display: flex;
  color: rgba(255, 115, 0, 0.8);
  font-size: 6rem;
  margin-bottom: 1rem;
}
@media (max-width: 620px) {
  .imgConteinerLink i {
    font-size: 3rem;
  }
}
.imgConteinerLink .git,
.imgConteinerLink .web {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0%;
  opacity: 0%;
  transition: opacity, bottom, 1s;
}
.imgConteinerLink .git {
  left: calc(50% + 6rem);
}
@media (max-width: 620px) {
  .imgConteinerLink .git {
    left: calc(50% + 3rem);
  }
}
.imgConteinerLink .web {
  right: calc(50% + 6rem);
}
@media (max-width: 620px) {
  .imgConteinerLink .web {
    right: calc(50% + 3rem);
  }
}
.imgConteinerLink:hover .git, .imgConteinerLink:hover .web {
  bottom: calc(48% - 3rem);
  opacity: 100%;
}
@media (max-width: 620px) {
  .imgConteinerLink {
    height: 25rem;
  }
}
@media (max-width: 480px) {
  .imgConteinerLink {
    height: 15rem;
    margin: 0.5rem 0 0.5rem 0;
  }
}
@media (max-width: 896px) {
  .imgConteinerLink {
    margin: 2rem 0 2rem 0;
  }
}

.tec {
  width: 95%;
  margin: auto;
  margin-top: 10rem;
  padding-bottom: 10rem;
