.start {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    .containerTitle {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;

        .titleN, .titleL {
            color: $color5;
            font-family: $font3;
            font-size: 10rem;
            margin-top: 0;
            margin-bottom: 1rem;
            text-transform: uppercase;

            @include desktop {
                font-size: 8rem;
            }

            @include lg-tablet{
                font-size: 6rem;
            }

            @include tablet{
                font-size: 5rem;
            }
        }

        .titleSep {
            margin: 1rem 2rem;

            @include telefono {
                display: none;
            }
        }

        @include lg-telefono {
            flex-direction: column;
        }
    }

    .containerSubtitle {
        display: flex;
        align-items: center;
        justify-content:flex-end;
        width: 100%;

        .subtitle {
            color: $color5;
            font-family: $font3;
            font-size: 4rem;
            margin: 0;
            text-transform: uppercase;

            @include desktop {
                font-size: 3rem;
            }

            @include lg-tablet{
                font-size: 2rem;
            }

            @include tablet {
                font-size: 2rem;
            }

            @include telefono {
                font-size: 2rem;
            }
        }
    }
}