::-webkit-scrollbar {
  width: 1.2rem;
}

::-webkit-scrollbar-corner {
  background-color: $color6;
  border-top-left-radius: -10px;
  border-top-right-radius: -10px;  
}

::-webkit-scrollbar-track {
  background-color: $color6;
}

::-webkit-scrollbar-thumb {
  background-color: $color5;
  border-radius: 10px;
  border: 2px solid $color6;
}