.pro {
    @media (max-width: $desktop) {
        margin-top: 2rem;
    }
}

.proImgConteiner {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    margin: 0 0 20rem 0;

    @include desktop {
        margin: auto;
    }
}

.imgConteinerLink {
    height: 35rem;
    margin: 5rem 0 5rem 0;
    position: relative;
    border-radius: 2rem;

    .imgProject {
        height: 100%;
        border-radius: 2rem;
        opacity: 100%;
        transition: opacity, 1s;
    }

    &:hover .imgProject {
        opacity: 40%;
    }

    a {
        text-decoration: none;
        font-size: 2rem;
        color: $color2;

        @include lg-telefono {
            font-size: 1rem;
        }
    }

    i {
        display: flex;
        color: $color4Op;
        font-size: $icon;
        margin-bottom: 1rem;

        @include lg-telefono {
            font-size: calc($icon / 2);
        }
    }

    .git,
    .web {
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom: 0%;
        opacity: 0%;
        transition: opacity, bottom, 1s;
    }

    .git {
        left: calc(50% + $icon);

        @include lg-telefono {
            left: calc(50% + ($icon / 2));
        }
    }

    .web {
        right: calc(50% + $icon);
        
        @include lg-telefono {
            right: calc(50% + ($icon / 2));
        }
    }

    &:hover .git,
    &:hover .web {
        bottom: calc(48% - calc($icon / 2));
        opacity: 100%;
    }

    @include lg-telefono {
        height: 25rem;
    }

    @include telefono {
        height: 15rem;
        margin: 0.5rem 0 0.5rem 0;
    }

    @include lg-tablet {
        margin: 2rem 0 2rem 0;
    }
}