.con {
    padding-bottom: 5rem;
    padding-top: 5rem;


    .consoleExter {
        width: 81.8rem;
        height: 61.8rem;
        margin: auto;
        background-color: $bTerminale3;
        border: 1rem solid $bTerminale3;
        border-radius: 20px;

        @include tablet {
            width: 61.5rem;
            height: 46.5rem;
            border: .75rem solid $bTerminale3;
        }

        @include lg-telefono{
            width: 46.4rem;
            height: 35.15rem;
            border: .70rem solid $bTerminale3;
        }

        @include sm-telefono{
            width: 31.4rem;
            height: 31.4rem;
            border: .70rem solid $bTerminale3;
        }
    }

    .console {
        position: relative;
        background-color: $bTerminale2;
        width: 80rem;
        height: 60rem;
        margin: auto;
        border: 3.25rem solid $bTerminale2;
        border-radius: 10px;

        @include tablet {
            width: 60rem;
            height: 45rem;
            border: 2.25rem solid $bTerminale2;
        }

        @include lg-telefono{
            width: 45rem;
            height: 33.75rem;
            border: 1.9rem solid $bTerminale2;
        }

        @include sm-telefono{
            width: 30rem;
            height: 30rem;
            border: 1.8rem solid $bTerminale2;
        }

        .onConsole {
            width: 1.5rem;
            height: 1.5rem;
            position: absolute;
            bottom: -3%;
            left: -2.25%;
            background-color: $color4;
            border-radius: 50%;

            @include tablet {
                width: 1.2rem;
                height: 1.2rem;
            }
            
            @include tablet {
                width: 1rem;
                height: 1rem;
            }

            @include sm-telefono{
                bottom: -3%;
                left: -3%;
            }
        }

        .consoleInter {
            width: 73.5rem;
            height: 53.5rem;
            margin: auto;
            background-color: $cTerminale;
            border: 2px solid $bTerminale1;
            border-radius: 25px;

            @include tablet {
                width: 55rem;
                height: 40rem;
                border: 2px solid $bTerminale1;
            }

            @include lg-telefono{
                width: 41.2rem;
                height: 29.95rem;
                border: 2px solid $bTerminale1;
            }

            @include sm-telefono{
                width: 26.4rem;
                height: 26.4rem;
                border: 2px solid $bTerminale1;
            }
        }
    }

    @include tablet {
        padding-top: 2.5rem;
    }
}



.form {
    padding-top: 7rem;
    width: 100%;
    height: 100%;
    display: block;

    .campo {
        color: $color4;
        width: 90%;
        height: 4rem;
        padding-top: 1.5rem;
        margin: auto;
        display: flex;
        gap: 1rem;
        flex-wrap: nowrap;
        align-content: flex-end;
        justify-content: center;

        .campoLabel {
            width: 20%;
            height: 100%;
            font-family: $font1;
            font-size: 2rem;
            text-align: left;
            border-style: none;
            text-align: right;

            &:last-of-type {
                padding-top: .2rem;
            }

            @include tablet {
                font-size: 1.8rem;
            }

            @include lg-telefono{
                font-size: 1.4rem;
            }
            
            @include sm-telefono{
                font-size: 1.1rem;
                width: 25%;
            }
        }

        .campoField {
            width: 80%;
            height: 100%;
            font-family: $font1;
            font-size: 2rem;
            color: $color4;
            background-color: $cTerminale;
            border: 0;
            outline: none;
            padding-bottom: .2rem;

            &:-webkit-autofill,
            &:-webkit-autofill:hover,
            &:-webkit-autofill:focus {
                -webkit-box-shadow: 0 0 0px 1000px $cTerminale inset;
            }

            &:-webkit-autofill {
                -webkit-text-fill-color: $color4;
            }

            @include tablet {
                font-size: 1.8rem;
            }

            @include lg-telefono{
                font-size: 1.4rem;
            }

            @include sm-telefono{
                font-size: 1.1rem;
                width: 70%;
                padding-bottom: .8rem;
            }
        }

        .campoField--textarea {
            height: 18rem;
            padding-bottom: 2rem;
            margin-bottom: 2rem;

            @include tablet {
                height: 10rem;
            }

            @include sm-telefono{
                height: 6rem;
            }
        }

        .btn {
            margin-top: 18rem;
            height: 4rem;
            width: 8rem;
            color: $color4Op;
            font-family: $font1;
            font-size: 2rem;
            font-weight: bold;
            background-color: $bTerminale2;
            border: 3px solid $bTerminale1;
            border-radius: 5px;

            &:hover {
                color: $color4;
                border-color: $bTerminale3;
                cursor: pointer;
            }

            @include tablet {
                margin-top: 12rem;
                font-size: 1.8rem;
            }

            @include lg-telefono {
                margin-top: 8rem;
                font-size: 1.4rem;
                width: 7rem;
                height: 3.5rem;
            }

            @include sm-telefono {
                margin-top: 6rem;
                font-size: 1.2rem;
                width: 6rem;
                height: 3rem;
            }
        }
    }

    @include tablet {
        padding-top: 2.5rem;
    }
}

.redes {
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    margin-top: 5rem;
    font-size: $icon2;

    i {
        color: $color4;
    }
}

.textConsole {
    color: $color4;
    font-family: $font1;
    font-size: 2rem;
    padding-top: 5rem;
    padding-left: 3rem;
    text-align: left;

    @include tablet {
        font-size: 1.8rem;
    }

    @include lg-telefono{
        font-size: 1.4rem;
    }

    @include sm-telefono{
        font-size: 1.1rem;
    }
}