@font-face {
    font-family: 'foundationtitleshandmedium';
    src: url('font/foundationtitleshand-v0.85-webfont.woff2') format('woff2'),
        url('font/foundationtitleshand-v0.85-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'kerox-noncommercialregular';
    src: url('font/kerox-noncommercial-webfont.woff2') format('woff2'),
         url('font/kerox-noncommercial-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

// Tamaño de Media Queries
$sm-telefono: 450px;
$telefono: 480px;
$lg-telefono: 620px;
$tablet: 768px;
$lg-tablet: 896px;
$desktop: 1024px;

//Fuentes
$font1: 'helvetica nuew', helvetica;
$font2: 'foundationtitleshandmedium';
$font3: 'kerox-noncommercialregular';


//Tamaño Iconos
$icon: 6rem;
$icon2: 3rem;

//Colores
$color1: #f8f0cd;
$color2: #f5f5f5;
$color2Op1: rgba(255, 255, 255, 0.4);
$color2Op2: rgba(255, 255, 255, 0.8);
$color3: #292929;
$color4: #ff7300;
$color4Op: rgba(255, 115, 0, 0.8);
$color5: rgb(255, 211, 144);
$color6: #151515;
$color7: #030303;
$cTerminale: #030b0c;
$bTerminale1: #191919;
$bTerminale2: #262626;
$bTerminale3: #2c2c2c;