header {
    width: 100%;
    height: 5rem;
    position: fixed;
    background-color: $color6;
    z-index: 9999;
}

.containerHeader {
    max-width: 140rem;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    padding-right: 2rem;
    padding-left: 2rem;

    .nav {
        max-width: 140rem;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 3.5rem;
        height: 100%;

        a {
            text-decoration: none;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0% 100%;
            background-repeat: no-repeat;
            background-size: 0% 2px;
            transition: background-size .3s;
            font-size: 2rem;
            color: $color5;

            &:hover {
                background-size: 100% 2px;
                font-weight: bold;
                color: $color4;
            }

            @include lg-telefono {
                font-size: 1.5rem;
            }
        }

        .focusing {
            background-position: 0% 100%;
            background-size: 100% 2px;
            font-weight: bold;
            color: $color4;
        }

        @include tablet {
            flex-direction: column;
            align-items: flex-end;
            background-color: $color6;
            position: fixed;
            overflow-y: auto;
            margin: 0;
            right: -80%;
            height: 27rem;
            top: 5rem;
            padding-bottom: 3.5rem;
            padding-right: 2rem;
            transition: right 0.3s;
        }

        @include lg-telefono {
            height: 16rem;
            gap: 2rem;
            padding-bottom: 2rem;
        }
    }

    .logo {
        text-decoration: none;
        text-align: center;
        vertical-align: middle;
        font-family: $font3;
        font-size: 3rem;
        color: $color5;
        padding-bottom: .5rem;
        border: 0.1rem solid $color5;
        border-radius: 50%;
        width: 4.5rem;

        &:hover {
            color: $color4;
            border: 0.1rem solid $color4;
        }

        @include lg-telefono {
            font-size: 2rem;
            width: 2.97rem;
        }
    }

    .nav-toggle {
        color: $color5;
        background: none;
        border: none;
        font-size: 30px;
        cursor: pointer;
        display: none;

        @include tablet {
            display: block;

            
            &:focus:not(:focus-visible) {
                outline: none;
            }
        }
    }

    @include tablet {
        .nav-menu_visible {
            padding-right: 2rem;
            right: 0;
            padding-left: 1rem;
        }
    }
}